import { dia, elementTools } from "../global";

var boundaryTool = new elementTools.Boundary({
  padding: 10,
  rotate: true,
  useModelGeometry: true,
});
var removeButton = new elementTools.Remove();

var toolsView = new dia.ToolsView({
  tools: [boundaryTool, removeButton],
});

export default {
  function(paper) {
    var toggler_right = document.querySelector(
      ".rf-qna-flow-right-bar-toggler"
    );
    var toggler_left = document.querySelector(".rf-qna-flow-left-bar-toggler");

    toggler_right.addEventListener("click", function () {
      toggler_right.parentElement.classList.toggle(
        "rf-qna-flow-right-bar-toggler-collapsed"
      );
    });

    toggler_left.addEventListener("click", function () {
      toggler_left.parentElement.classList.toggle(
        "rf-qna-flow-left-bar-toggler-collapsed"
      );
    });

    paper.on("cell:pointerclick", function (elView) {
      console.dir(elView);

      if (paper.paperType === "qna") {
        elView.addTools(toolsView);
      }
    });
  },
};
