function setPorts(el, digits) {
  let width = 0;
  // Optional ports
  const digitPorts = digits.map((digit, index) => {
    const x = index * (portWidth + portGap);
    width = x + portWidth;
    return {
      id: `${digit}`,
      group: "digits",
      attrs: {
        portLabel: {
          text: `${digit}`,
        },
      },
      args: {
        x,
        y: "100%",
      },
    };
  });
  if (digitPorts.length > 0) {
    width += portGap;
  }

  // Required port.
  const fallbackPort = {
    id: "fallback",
    group: "digits",
    size: { width: portWidth * 2, height: portHeight },
    attrs: {
      portLabel: {
        text: "fallback",
      },
    },
    args: {
      x: width,
      y: "100%",
    },
  };

  const connectPort = {
    position: {
      name: "top", // Layout name
      args: {}, // Arguments for port layout function, properties depend on type of layout
    },
    label: {
      position: {
        name: "top",
        args: { y: 6 },
      },
      markup: [
        {
          tagName: "text",
          selector: "label",
        },
      ],
    },
    attrs: {
      body: {
        magnet: "passive",
        width: 15,
        height: 15,
        stroke: "black",
        x: -8,
        y: -8,
      },
      label: {},
    },
    markup: [
      {
        tagName: "rect",
        selector: "body",
      },
    ],
  };

  width += 2 * portWidth;

  el.prop(["ports", "items"], [...digitPorts, fallbackPort, connectPort], {
    rewrite: true,
  });
  el.prop(["size", "width"], width);
}

