import { shapes, gridSize } from "../global";

export default {
  createLink: (source, target) => {
    return new shapes.standard.Link({
      source: source,
      target: target,
    }).router("manhattan", {
      step: gridSize,
      perpendicular: true,
      startDirections: ["top", "bottom"],
      endDirections: ["top"],
    });
  },
  setAttribute: (el, attr) => {
    el.attr(attr);
  },
};
