import { dia, shapes, gridSize } from "../global";

export default {
  createPaper: function (graph, drawGridName, backgroundColor) {
    return new dia.Paper({
      model: graph,
      cellViewNamespace: shapes,
      width: "100%",
      height: "100%",
      gridSize: gridSize,
      drawGrid: drawGridName ? { name: drawGridName } : false,
      async: true,
      sorting: dia.Paper.sorting.APPROX,
      background: { color: backgroundColor ? backgroundColor : "#F3F7F6" },
      defaultLink: () => new shapes.standard.Link(),
      validateConnection: (sv, _sm, tv, _tm) => sv !== tv,
      linkPinning: false,
      defaultAnchor: {
        name: "perpendicular",
      },
    });
  },
};
